.total {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.total .result {
  font-size: 100px;
}

.total .options {
  list-style: none;
}

.total .options .btn {
  padding: 8px;
  font-size: 30px;
}

.total .options .btn.male {
  padding: 8px 8px 6px 10px;
}

.total .options .btn.female {
  padding: 8px 9px 6px 9px;
}
